import axios from 'axios'
import { Message } from 'element-ui'
import store from '../store'
import { getToken } from '../utils/auth'
import qs from 'qs';
import router from '../router'

// axios
const service = axios.create({
  baseURL: process.env.BASE_API, // apibase_url
  timeout: 5000 // 
})
//console.log(process.env);
// request
service.interceptors.request.use(config => {
  // Do something before request is sent
  if (store.getters.token) {
    //config.headers['M-Token'] = getToken() // token--['X-Token']key 
  }
  return config
}, error => {
  // Do something with request error
  console.log(error) // for debug
  Promise.reject(error)
})

// respone
service.interceptors.response.use(response => {
  /**
   * responsecodecode
   * xmlhttprequest  error
   */

  const res = response.data;
  if (res.status === 0 || res.errorNum > 0 ) {
    if(res.errorNum == 403){
      Message({
        message: res.message,
        type: 'error',
        duration: 5 * 1000
      })
      localStorage.clear()
      router.push({path:'/login'});
    }else{
      Message({
        message: res.message,
        type: 'error',
        duration: 5 * 1000
      });
  
      return Promise.reject('error');
    }
    
  } else {
    return res.result;
  }
}, error => {
  console.log('err' + error)// for debug
  Message({
    message: error.message,
    type: 'error',
    duration: 5 * 1000
  })
  return Promise.reject(error)
})

const httpApi = (opts) => {
  let Public = { //
    'v': Date.now()
  }
  if (store.getters.token) {
    Public.token = getToken() // token--['X-Token']key 
  }
  //console.log('----------');
  //console.log(opts.data)
  let httpDefaultOpts = {
    method: opts.method || 'get',
    //baseURL: 'http://localhost/api',
    url: opts.url,
    params: Object.assign(Public, opts.data),
    data: qs.stringify(Object.assign(Public, opts.data)),
    headers: opts.method == 'get' ? {
      'X-Requested-With': 'XMLHttpRequest',
      'Accept': 'application/json',
      'Content-Type': 'application/json; charset=UTF-8'
    } : {
      'X-Requested-With': 'XMLHttpRequest',
      'Content-Type': 'application/x-www-form-urlencoded; charset=UTF-8'
    }
  };

  if(opts.method == 'get'){
    delete httpDefaultOpts.data;
  }else{
    delete httpDefaultOpts.params;
  }
  
  let httpPromise = new Promise((resolve, reject) => {
    //console.log(httpDefaultOpts);
    service(httpDefaultOpts).then((res) => {
        //console.log(res);
        resolve(res);
    }).catch((response) => {

      reject(response);
    });
  });
  return httpPromise;

}


export default httpApi



